@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  background-color: #36393f;
  /* background-color: #ddd; */
  margin: 0;
  font-family: 'Nunito', sans-serif;
  color: #eee;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Small scrollbars for some panes */
.scrollable {
  overflow: hidden;
}

.scrollable:hover {
  overflow-y: overlay;
}

.scrollable::-webkit-scrollbar {
  width: .25rem;
}

.scrollable::-webkit-scrollbar-thumb {
  background-color: #666;
}

.scrollable::-webkit-scrollbar-track {
  margin: .75rem 0;
  background: none;
}

/* Global style for all modals */
.modal-dialog {
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.modal-header {
  border-bottom: 1px solid #2f3136;
}

/* Close buttons */
.close {
  color: #eee;
}

.close:hover {
  color: #eee;
  opacity: .75;
}

.header {
  display: flex;
  align-items: center;
  min-width: 100%;
  background-color: #2f3136;
  box-shadow: 0px 0px 8px 2px #282828;
  padding: 8px 12px;
}

.user {
  display: flex;
  align-items: center;
}

.username {
  color: #ddd;
  margin: 0px;
  font-size: 1.5rem;
  font-weight: 600;
}

.user > .avatar {
  display: flex;
  flex-direction: column;
  height: 45px;
  margin-right: 10px;
  border-radius: 50%;
}

.navbar-collapse {
  margin-left: 24px;
  margin-right: 24px;
}

.search-input, .search-input:focus {
  min-width: 250px !important;
  background-color: #40444b;
  border-radius: .25rem 0 0 .25rem;
  color: #ddd;
  border: none;
}

.search-input::-webkit-input-placeholder {
  color: #999;
}

.search-input::-moz-placeholder {
  color: #999;
}

.search-input:-ms-input-placeholder {
  color: #999;
}

.search-input::-ms-input-placeholder {
  color: #999;
}

.search-input::placeholder {
  color: #999;
}

.search-icon {
  display: flex;
  align-items: center;
  margin: 0 2px;
  padding: 0 .3rem;
  border-radius: 0 .25rem .25rem 0;
  background-color: #7289DA;
}

.search-icon:hover {
  background-color: rgb(95, 115, 187);
}

.search-icon img {
  width: 1.75rem;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 18px;
}

.media-container {
  position: relative;
  margin: 6px;
}

.media-container:hover {
  cursor: pointer;
}

.media-container .media {
  max-height: 250px;
  box-shadow: 0px 2px 4px 2px #303030;
}

.media-container .youtube {
  display: flex;
  align-items: center;
  justify-content: center;
}

.media-container .youtube-icon {
  position: absolute;
  width: 70px;
}

.media-container .gif {
  position: absolute;
  right: 0;
  width: 28px;
  margin: 7px;
  border-radius: 50%;
  box-shadow: 0.5px 1px 3px #383838;
}

.fade.lightbox {
  background-color: #00000099;
}

.lightbox .modal-dialog {
  max-width: 90vw;
}

.lightbox .modal-content {
  background-clip: unset;
  background-color: transparent;
  border: none;
}

.lightbox .modal-body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0;
  padding: 0;
}

.lightbox .media {
  max-width: 73vw;
  max-height: 90vh;
}

.lightbox .media > iframe {
  width: 65vw;
  height: 65vh;
}

.lightbox .gif {
  position: absolute;
  right: 0;
  width: 28px;
  margin: 7px;
  border-radius: 50%;
  box-shadow: 0.5px 1px 3px #383838;
}

.lightbox-info {
  max-width: 20vw;
  max-height: 92vh;

  margin-left: 1rem;  
  word-wrap: break-word;
  border-radius: .5rem;
  background-color: #36393f;
  box-shadow: 2px 4px 6px 2px #00000055;
}

.lightbox-info-section {
  margin: .5rem .75rem;
}

.lightbox-info-section.footer {
  margin-top: .1rem;
  font-size: .76rem;
  color: #999;
}

.lightbox-info-section.id {
  float: left;
}

.lightbox-info-section.saved-date {
  float: right;
}

.lightbox-info hr {
  border-color: #2f3136;
  background-color: #2f3136;
  color: #2f3136;
  margin: .5rem 0;
}

.lightbox-info .tags {
  padding: 0 .5rem .5rem .5rem;
}

.lightbox-info a {
  display: block;
  color: cornflowerblue;
}

.lightbox-info a:hover {
  color: rgb(153, 189, 255);
}

.lightbox .close {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.tag {
  display: inline-block;
  margin: .2rem;
  padding: .3rem .5rem;
  border-radius: .75rem;
  background-color: #292b2f;
  color: #ddd;
  box-shadow: 1px 1px 1px #00000066;
}

.tag.clickable:hover {
  cursor: pointer;
  background-color: #3d3f46;
}

.pager {
  height: 7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: .9rem;
}

.pager button {
  color: #ddd;
  border: none;
  margin: 1rem;
  padding: .3rem .5rem;
  border-radius: .5rem;
  background-color: #4f545c;
  box-shadow: 1px 2px 4px #00000044;
}

.pager button:hover {
  background-color: #646a74;
}

.pager button > img {
  width: 1.4rem;
}

.pager button:focus {
  outline: none;
}

.pager button.disabled {
  opacity: .5;
}

.pager button.disabled:hover {
  background-color: #4f545c;
  opacity: .5;
}

.tags-button {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 4rem;
  height: 4rem;
  margin: 2rem;
  z-index: 20;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  background-color: #7289DA;
  box-shadow: 2px 4px 10px 0 #222;
}

.tags-button:hover {
  cursor: pointer;
  background-color: rgb(95, 115, 187);
}

.tags-button > img {
  width: 2.4rem;
}

.tags {
  background-color: #36393f;
}

.tags .modal-content {
  color: #ddd;
}

.tags .modal-body {
  max-height: 36rem;
}

